html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
}

a {
  text-decoration: none;
}

a:active,
a:hover {
  outline: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus {
  outline: 0;
}

img,
audio,
video {
  max-width: 100%;
  height: auto;
}

audio,
canvas,
iframe,
video,
img,
svg {
  vertical-align: middle;
}

iframe {
  border: 0;
}

textarea {
  resize: none;
  /* remove the resize handle on the bottom right */
  overflow: auto;
  vertical-align: top;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
}

input,
textarea,
select,
button {
  outline: none;
  border: none;
  font-size: 100%;
  margin: 0;
}

button,
input {
  line-height: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
  text-align: left;
}

nav {
  background: #fafafa;
  box-shadow: 0.1rem 0.1rem 0.1rem #c0c4cc;
  position: sticky;
  top: 0;
}

nav ul {
  display: flex;
  flex-direction: row;
}

nav ul li {
  padding: 0.5rem 1rem;
}

nav ul li:hover {
  background: rgba(25,25,25,0.85);
}

nav ul li:hover a {
  color: #faf5f0;
}

nav ul li a {
  color: rgba(25,25,25,0.85);
}

.address {
  padding: 1rem;
}

.address a {
  color: rgba(15,15,15,0.9);
}

.address a:hover {
  color: #ff7b00;
}

.address .fab {
  color: #c0c4cc;
}

.fa-instagram-square:hover {
  color: #569af3;
}

.fa-facebook:hover {
  color: #006eff;
}

.fa-vk:hover {
  color: #444ce0;
}

.fa-youtube:hover {
  color: #f00;
}

.fa-linkedin:hover {
  color: #0a66c2;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.logo {
  display: flex;
}

.logo a {
  margin: 0 0.5rem;
}

.logo img {
  height: 5rem;
  width: 5rem;
  border-radius: 50%;
}

.right p {
  text-align: end;
  align-content: center;
}

.hero {
  background: url("https://picsum.photos/2000/1000/?blur") no-repeat 0 0/cover;
  height: 30rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero h1 {
  color: #fff;
  font-size: calc(10vw -30%);
  text-shadow: 1px 1px 2px rgba(15,15,15,0.9);
}

html,
body {
  font: 400 10px/1.5 Ubuntu, Helvetica, Arial, sans-serif;
  color: #313233;
}

body,
input,
textarea,
button {
  font-family: 'El Messiri', sans-serif;
  text-align: center;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.7rem;
}

h3 {
  font-size: 1.45rem;
}

h4 {
  font-size: 1.3rem;
}

h5 {
  font-size: 1.15rem;
}

p,
li,
em,
b,
a,
details,
summary,
span {
  font-size: 1rem;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}

h2,
h3 {
  margin: 1em 0;
}

a,
a:hover {
  text-decoration: none;
}

.container {
  max-width: 75vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.iconcards {
  font-size: 2rem;
}

.article-heading {
  padding: 1rem;
}

.article-heading h3 {
  margin: 1rem 0 0;
  font-size: 2rem;
}

.article-heading h5 {
  font-style: italic;
  margin-bottom: 1em;
  color: #606266;
}

.article-heading h6 {
  text-align: end;
}

article {
  padding: 4rem;
}

article img {
  margin-bottom: 2em;
}

article:nth-child(even) {
  background: #faf5f0;
}

article:nth-child(2) h3::before,
article:nth-child(3) h3::before {
  content: 'New! ';
  color: #c30f30;
}

article p,
article summary {
  text-justify: inter-word;
  text-align: justify;
}

section h2 {
  font-size: calc(5vw);
  color: #ff7b00;
}

h2.news::after {
  content: 'Авторитетно о главном';
  font-size: calc(3vw);
  display: block;
  position: relative;
  height: 1.5em;
  color: #303133;
  border: 0.1rem outset groove #ff7b00;
  box-shadow: 0.05rem 0.05rem 0.752rem #909399;
  width: 40%;
  margin: 0 auto;
}

footer {
  background: rgba(25,25,25,0.85);
  color: #fafafa;
}

footer a {
  color: #fafafa;
}

footer a:hover {
  color: #ff7b00;
  text-shadow: 0.1rem 0.1rem 0.25rem rgba(25,25,25,0.85);
}

footer .fab:hover {
  text-shadow: 0.5rem 0.5rem 0.3rem rgba(15,15,15,0.9);
}

.middle {
  padding: 2rem 0;
}

.middle .links {
  display: flex;
  justify-content: space-evenly;
}

.middle a {
  text-transform: uppercase;
  font-size: calc(3vw);
}

.middle .sn-links {
  display: flex;
  justify-content: center;
}

.middle .sn-links li {
  text-align: center;
  margin: 1rem;
}

@media (min-width: 320px) {
  html {
    font-size: 14px;
  }
}

@media (min-width: 480px) {
  html {
    font-size: 16px;
  }
}

@media (min-width: 640px) {
  html {
    font-size: 16.5px;
  }
}

@media (min-width: 768px) {
  html {
    font-size: 17px;
  }
}

@media (min-width: 992px) {
  html {
    font-size: 17.5px;
  }
}

@media (min-width: 1200px) {
  html {
    font-size: 18.5px;
  }
}

@media (min-width: 1920px) {
  html {
    font-size: 20px;
  }
}

@media (min-width: 2560px) {
  html {
    font-size: 22px;
  }
}

@media (min-width: 3060px) {
  html {
    font-size: 27.5px;
  }
}

@media (min-width: 1440px) {
  .container {
    width: 1410px;
  }
}

@media (min-width: 1920px) {
  .container {
    width: 1800px;
  }
}

@media (min-width: 2560px) {
  .container {
    width: 2400px;
  }
}